<template>
  <div>
    <el-row>
      <el-col :span="24">
        <h4 class="title">
          <a class="pull-right btns" href="http://www.rainrain.xin:12345/studentdb/score/export/all">EXCEL</a>
        </h4>
        <div style="clear: both"></div>
      </el-col>
      <el-table
        :data="scoreData"
        border style="width: 100%">
        <el-table-column prop="id" label=""></el-table-column>
        <el-table-column prop="student_id" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="className" label=""></el-table-column>
        <el-table-column prop="grade" label=""></el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button type="primary" size="small"  @click="scoreEdit(scope.$index, scope.row)"></el-button>
            <el-button type="danger" size="small" @click="scoreDelete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block text-center m-t-sm" style="height:70px;">
        <el-pagination
          @size-change="sizeChange"
          @current-change="currentChange"
          :page-sizes="[10,20,30,40]"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="page.totalRecords">
        </el-pagination>
      </div>
      <!---->
      <el-dialog title="" :visible="changeScoreform" size="tiny" :modal-append-to-body='false' @close='closeDialog'>
        <el-form ref="editForm" :model="editForm" label-width="80px">
          <el-form-item label="">
            <el-input  v-model="editForm.student_id" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input  v-model="editForm.name" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="editForm.class_id" value-key="id" disabled="disabled">
              <el-option v-for="item in listArry"  :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editForm.grade"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="scoreChange()"></el-button>
            <el-button  @click="changeScoreform=false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-row>

  </div>
</template>

<script>
  import Main from '../../api.js';
    export default {
      name: "grade-manage",
      data() {
        return {
          scoreData:[],
          listArry:[], //
          changeScoreform:false,  //
          page: {
            pageSize: 10, //,  10
            totalRecords: 0, //
            totalPages: 0, //
            pageNum:0
          },
          editForm:{
            id:"",
            student_id:"",
            class_id:"",
            grade:"",
          }
        }
      },
      mounted(){
        this.init()
      },
      methods: {
        init(){
          this.scoreData=[];
          let {pageNum,pageSize} = this.page;
          this.$http.get(Main.getallCoursescore(pageNum,pageSize)).then(res =>{
            let{errCode,dataList,totalRecords}=res.data;
            console.log(res.data);
            this.page.totalRecords=totalRecords;
            if(errCode==0){
              const scoreArray=dataList;
              this.scoreData=scoreArray;
            }else{
            }
          }, response => {
            // error callback
          })
        },
        //        
        closeDialog(){
          this.changeScoreform = false;
        },
        //        
        sizeChange(val) {
          this.page.pageSize = val;
          this.init();
        },
        //
        currentChange(val) {
          this.page.pageNum=val-1;
          console.log(this.page.pageNum);
          this.init();
        },
        // 
        scoreEdit(index,row){
          // id
          this.changeScoreform = true;
          this.$http.get(Main.getallCourse()).then(res =>{
            console.log(res.data);
            let {errCode,dataList,errMsg}=res.data;
            if(errCode==0){
              this.listArry=dataList;
            }else{
            }
          }, response => {
            // error callback
          })
          this.editForm = Object.assign({}, row);
        },

        // 
        scoreChange(){
          let {grade}=this.editForm;
          if(grade !==''){
            this.$http.put(Main.changeScore(),this.editForm).then(res => {
              let {errCode,errMsg} = res.data;
              if(!errCode==1){
                this.init();
                this.$message.success(errMsg);
                this.changeScoreform  = false;
              }else{
                this.$message.error(errMsg);
              }
            }, response => {

            });
          }else{
            this.$message.error('');
          }
        },
        // id
        scoreDelete(user){
          this.$confirm(' ' + user.name + ', ?', '', { type: 'warning' })
            .then(() => { // 
              this.$http.delete(Main.deleteScore(user.id)).then((response) => {
                this.$message.success('' + user.name + '!');this.init()})
                .catch((response) => {
                  this.$message.error('!');
                });
            }) .catch(() => {
            this.$message.info('!');
          });
        },
        // excel
      }
    }
</script>

<style scoped>
  /**/
  .title{
    height:40px;
    line-height:40px;
    background: #fff;
    color:rgb(32, 160, 255);
    font-size:16px;
    font-family:"PingFang SC";
    padding:10px 15px;
    border-left:3px solid rgb(32, 160, 255);
  }
  .btns{
    color: #fff;
    background-color: #67c23a;
    border-color: #67c23a;
    display:inline-block;
    padding:0 20px;
    border-radius: 4px;
    font-weight:500;
    text-decoration: none;
    font-size:14px;
  }
  .btns:hover{
    background: #85ce61;
    border-color: #85ce61;
    color: #fff;
  }
</style>



