<template>
  <div>
    <el-row>
      <el-col :span="24" class="text-right m-b-xs">
        <el-button type="primary" @click="getAdd()"></el-button>
      </el-col>
      <el-table class="m-t-sm" :data="tableData" border style="width: 100%">
        <el-table-column prop="id" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="credit" label=""></el-table-column>
        <el-table-column prop="ctime" label=""></el-table-column>
        <el-table-column prop="type" label=""></el-table-column>
        <el-table-column prop="teacher_id" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="courseEdit(scope.$index, scope.row)"></el-button>
            <el-button type="danger" size="small" @click="courseDelete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <!---->
    <el-dialog title="" :visible="dialogFormVisible" size="tiny" :modal-append-to-body='false' @close='closeDialog'>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.id" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.credit"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.ctime"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.type"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.teacher_id" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="editChange()"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible="addCourseform" size="tiny" :modal-append-to-body='false' @close='closeDialog'>
      <el-form id="#addForm" ref="addForm" :model="addForm" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input  v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="credit">
          <el-input v-model="addForm.credit"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ctime">
          <el-input v-model="addForm.ctime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-input v-model="addForm.type"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addForm.teacher_id" value-key="id">
            <el-option v-for="item in techerArry"  :key="item.id" :label="item.tname" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="courseAdd()"></el-button>
          <el-button @click="addCourseform = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>

</template>

<script>
  import Main from '../api.js';
    export default {
        name: "course-manage",
        data(){
            return{
              url:'http://www.rainrain.xin:12345/studentdb/class/delete',
              tableData: [

              ],
              techerArry:[],
              dialogFormVisible: false,
              addCourseform:false,
              form: {
                id:'',
                name: '',
                credit: '',
                ctime: '',
                type: '',
                teacher_id:''
              },
              addForm:{
                name: '',
                credit: '',
                ctime: '',
                type: '',
                teacher_id:"2014124201"
              },
            }
        },
      // 
      mounted(){
        this.init();
      },
      methods:{
        init(){
          this.tableData=[];
          this.$http.get(Main.getCourse()).then(res => {
            const data=res.data;
            console.log(data);
            const code =data.errCode;
            if(code==0){
              const listArray=data.dataList;
              this.tableData=listArray;
            }else{
            }
          }, response => {
            // error callback
          });
        },
//        
        closeDialog(){
          this.dialogFormVisible = false;
          this.addCourseform = false;
        },
//        
        courseDelete(user){
          this.$confirm(' ' + user.name + ', ?', '', { type: 'warning' })
            .then(() => { // 
              this.$http.delete(Main.deleteCourse(user.id)).then((response) => {
                  this.$message.success('' + user.name + '!');this.init()})
                .catch((response) => {
                  this.$message.error('!');
                });
            }) .catch(() => {
            this.$message.info('!');
          });
        },
//        
        getAdd(){
          this.addCourseform = true;
          this.$http.get(Main.gettMsg()).then(res => {
            let {errCode,dataList,errMsg}=res.data;
            if(errCode==0){
              this.techerArry = dataList
            }else{
              alert(errMsg);
            }
          }, response => {
          });
        },
//        
        courseAdd(){
         let addlist=this.addForm;
         let {name,credit,ctime,type}=addlist;
         if(name==""||credit==""||ctime==""||type==""){
            this.$message.error('');
          }else{
           this.$http.post(Main.addCourse(),this.addForm).then(res => {
             this.$set(this.addForm,{});
             this.init();
             this.addCourseform = false;
           }, response => {
           });
         }
        },
//        
        courseEdit (index, row) {
          this.dialogFormVisible = true;
          this.form = Object.assign({}, row);
        },
//        
        editChange(){
          let changelist=this.form;
          let {name,credit,ctime,type}=changelist;
          if(name==""||credit==""||ctime==""||type==""){
            this.$message.error('');
          }else{
            this.$http.put(Main.changeCourse(),this.form).then(res => {
              this.init();
              this.dialogFormVisible = false;
            }, response => {
            });
          }
        }
      },
    }
</script>

<style scoped>

</style>



