<template>
  <div>
    <el-row>
      <el-col :span="24" class="text-right m-b-xs">
        <el-button type="primary" @click="addStudent()"></el-button>
      </el-col>
      <el-table :data="studentData" border style="width: 100%">
        <el-table-column prop="id" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="age" label=""></el-table-column>
        <el-table-column prop="sex" label=""></el-table-column>
        <el-table-column prop="major" label=""></el-table-column>
        <el-table-column prop="depart" label=""></el-table-column>
        <el-table-column prop="term" label=""></el-table-column>
        <el-table-column prop="year" label=""></el-table-column>
        <el-table-column prop="role" label=""></el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button type="primary" size="small" @click="studentEdit(scope.$index, scope.row)"></el-button>
            <el-button type="danger" size="small" @click="studentDelete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block" style="height:70px;">
        <el-pagination
          @size-change="sizeChange"
          @current-change="currentChange"
          :page-sizes="[10,20,30,40]"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="page.totalRecords">
        </el-pagination>
      </div>
      <!---->
      <el-dialog title="" :visible="addstudentForm" size="tiny" :modal-append-to-body='false' @close='closeDialog'>
        <el-form id="#addsForm" ref="addsForm" :model="addsForm" label-width="80px">
          <el-form-item label="" prop="id">
            <el-input  v-model="addsForm.id" max-length="10"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="addsForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-input v-model="addsForm.age"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-input v-model="addsForm.sex"></el-input>
          </el-form-item>
          <el-form-item label="" prop="major">
            <el-input v-model="addsForm.major"></el-input>
          </el-form-item>
          <el-form-item label="" prop="depart">
            <el-input v-model="addsForm.depart"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="addsForm.term" value-key="id">
              <el-option v-for="item in termArry"  :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="year">
            <el-input v-model="addsForm.year"></el-input>
          </el-form-item>
          <el-form-item label="" prop="role">
            <el-input v-model="addsForm.role" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="studentAdd()"></el-button>
            <el-button @click="addstudentForm = false;canceladdT('formt')"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible="editstudentForm" size="tiny" :modal-append-to-body='false' @close='closeDialog'>
        <el-form ref="editsForm" :model="editsForm" label-width="80px">
          <el-form-item label="">
            <el-input  v-model="editsForm.id" max-length="10" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.age"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.sex"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.major"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.depart"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="editsForm.term" value-key="id">
              <el-option v-for="item in termArry"  :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.year"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.role" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editsForm.passwd" disabled="disabled"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="studentcEdit()"></el-button>
            <el-button @click="editstudentForm = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-row>
  </div>
</template>

<script>
  import Main from '../api.js';
    export default {
        name: "student-manage",
      data(){
          return{
            studentData:[],
            addstudentForm:false, //
            editstudentForm:false, //
            termArry:[
              {id:'',name:''},
              {id:'',name:''},
            ],
            page: {
              pageSize: 10, //,  10
              totalRecords: 0, //
              totalPages: 0, //
              pageNum:0
            },
            addsForm:{
              id:'',
              name:'',
              age:"",
              sex:'',
              major:'',
              depart:'',
              term:'',
              year:'',
              role:'0'
            },
            editsForm:{
              id:'',
              name:'',
              age:"",
              sex:'',
              major:'',
              depart:'',
              term:'',
              year:'',
              role:'0',
              passwd:''
            },
          }
      },
      mounted(){
          this.init()
      },
      methods:{
          init(){
            this.studentData=[];
            let {pageNum,pageSize} = this.page;
            this.$http.get(Main.getStudent(pageNum,pageSize)).then(res =>{
              let {errCode,errMsg}=res.data;
              this.page.totalRecords=res.data.totalRecords;
              if(errCode==0){
                const studentArray=res.data.dataList;
                this.studentData=studentArray;
              }else{
                  alert(errMsg);
              }
            }, response => {
            })
          },
        // 
        sizeChange(val) {
          this.page.pageSize = val;
          this.init();
        },
        //
        currentChange(val) {
          this.page.pageNum=val-1;
          console.log(this.page.pageNum);
          this.init();
        },
          // 
          closeDialog(){
            this.addstudentForm = false;
            this.editstudentForm = false;
          },
          // 
          addStudent(){
              this.addstudentForm = true;
              console.log(this.addsForm)
          },
        // 
        studentAdd(){
            let studentList=this.addsForm;
            console.log(studentList);
            let {id,name,age,sex,major,depart,term,year} = studentList;
            if(id==''||name==''||age==''||sex==''||major==''||depart==''||term==''||year==''){
              this.$message.error('')
            }else{
              this.$http.post(Main.addStudent(),this.addsForm).then(res => {
                let {errCode,errMsg} = res.data;
                if(!errCode==1){
                  this.$set(this.addsForm,{});
                  this.init();
                  this.addstudentForm = false;
                }else{
                  this.$message.error(errMsg);
                }

              }, response => {
              });
            }
        },
        // 
        studentEdit(index,row){
            this.editstudentForm = true;
            this.editsForm = Object.assign({}, row);
        },
        // 
        studentcEdit(){
          let studenteList=this.editsForm;
          console.log(studenteList);
          let {name,age,sex,major,depart,term,year} = studenteList;
          if(name==''||age==''||sex==''||major==''||depart==''||term==''||year==''){
            this.$message.error("")
          }else{
            this.$http.put(Main.changeStudenet(),this.editsForm).then(res => {
              let {errCode,errMsg} = res.data;
              if(!errCode==1){
                this.init();
                this.editstudentForm  = false;
              }else{
                this.$message.error(errMsg);
              }
            }, response => {

            });
          }
        },
        // 
        studentDelete(user){
          this.$confirm(' ' + user.name + ', ?', '', { type: 'warning' })
            .then(() => { // 
              this.$http.delete(Main.deleteStudent(user.id,0)).then((response) => {
                this.$message.success('' + user.name + '!');this.init()})
                .catch((response) => {
                  this.$message.error('!');
                });
            }) .catch(() => {
            this.$message.info('!');
          });
        },
      }
    }
</script>

<style scoped>
  .el-pagination{
    text-align:center;
    margin-top:20px;
  }
  .el-table .cell{
    white-space:nowrap !important;
  }
</style>



